<script>
import { defineAsyncComponent } from 'vue'

const TypeWriter = defineAsyncComponent(() => import('./components/TypeWriter.vue'))
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'

import { library } from '@fortawesome/fontawesome-svg-core'
import { faCode, faList, faPhone, faEnvelope } from '@fortawesome/free-solid-svg-icons'
import { faHtml5, faInstagram, faTwitter, faLinkedin, faStackOverflow, faGithub, faWhatsapp } from '@fortawesome/free-brands-svg-icons'

import emailjs from 'emailjs-com'

import { loadFull } from "tsparticles";
import options from "./assets/tsParticles/lines.json"

library.add(faCode, faHtml5, faList, faPhone, faEnvelope, faInstagram, faTwitter, faLinkedin, faStackOverflow, faGithub, faWhatsapp)

export default {
  name: 'App',
  data(){
    return {
      options: options || {},
      validation: false,
      loading: false,
      form:{
        user_name: '',
        user_email: '',
        message: ''
      }
    }
  },
  components: {
    FontAwesomeIcon,
    TypeWriter
  },


  methods: {
    async particlesInit(engine) {
      await loadFull(engine);
    },

    sendEmail(){
      this.validationHandler()
      if(!this.validation){
        return
      }

      this.loading = true

      emailjs.sendForm('service_jl0jf8c', 'template_2jkom0e', this.$refs.form, '6XxH93U5gR2pAKCH8').then((result) => {
        this.form.user_name = ''
        this.form.user_email = ''
        this.form.message = ''
        alert('Formulario envíado.')
      }, (error) => {
        alert('Error al envíar formulario')
      }).then(() => {
        this.loading = false
      })
    },
    validationHandler(){
      if(this.$refs.form.checkValidity()){
        this.validation = true
      }else{
        this.validation = false
      }
    }
  },
  watch: {
    form: {
      handler(){
        this.validationHandler()
      },
      deep: true
    }
  }
}
</script>

